
import { defineComponent } from "@vue/runtime-core";
import ButtonDefault from "./ButtonDefault.vue";

export default defineComponent({
    name: 'Modal',
    components: { ButtonDefault },
    props: { show: {
        type: Boolean,
        required: true,
    },
    message: {
        type: String,
        required: true,
    },
    text: {
        type: String,
        default: '',
    },
    buttonYes: {
        type: String,
        default: 'Ok'
    },
    buttonNo: {
        type: String,
        default: ''
    },

    },
    emits: ['close']
})
