
import { defineComponent } from 'vue';
import Compass from '../components/Compass.vue';

export default defineComponent({
    name: 'Home',
    components: {
        Compass,
    },
    data: function() {
        return {
            parties: [] as any[],
        }},
    async created() {
        this.parties = (this as any).$store.state.parties
    },
    methods: {}
});
